                  placeholder="Placeholder" outlined></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-btn :color="tjm && tjm > minTjm ? 'cyan' : 'secondary'" required @click="() => hire()" height="56"
                  width="100%" elevation="2">Hire</v-btn>
              </v-col>
            </v-row>
          </v-form> -->
      </v-col>
      <v-col>
        <div class="fadeInAnimeRef mt-5">
          <p class="title-bio"><v-icon class="mb-1" color="cyan">mdi-account</v-icon> Biography</p>
          <p class="overline text-justify">
            I consider software development to be an <span class="keyword-color">art</span>, requiring complete dedication
